
import ModalToolbar from '../ModalToolbar.vue';
export default {
  components: { ModalToolbar },
  props: {
    id: {
      type: [Number, String],
    },
    float: {
      type: String, // example: right
    },
    small: {
      type: Boolean,
      default: false
    },
    label: {
      type: String,
      default: ''
    },
    color: {
      type: String,
      default: ''
    }
  },
  data:() => ({
    hint: false,
    isLoading: false,
    title: '',
    text: ''
  }),
  computed: {
    isIcon: function() {
      return this.label.length == 0
    }
  },
  methods: {
    async loadWiki() {
      const response = await this.$axios('wiki/' + this.id)
      this.isLoading = false;
      this.title = response.data.title
      this.text = response.data.text
    }
  }
}
