
/*
<entity-hover-async
  :id="item.item_id"
  :type="item.type"
>
  <template #activator="{ on, attrs }">
    <span class="px-2" v-bind="attrs" v-on="on">
      {{ item.title }}
    </span>
  </template>
</entity-hover-async>
*/
import { entityToRoute } from '@/helpers/router.js'
import EntityPinButton from './EntityPinButton.vue'
export default {
  components: { EntityPinButton },
  props: {
    id: {
      type: [String, Number],
      required: true
    },
    type: {
      type: String,
      required: true
    },
    openDelay: {
      type: Number,
      default: 250
    }
  },
  data:() => ({
    menu: false,
    entity: false,
    isLoading: false,
  }),
  computed: {
    imageSource() {
      return this.entity.entityImageProxyAllowed ? `${this.entity.entityImage}/thumb/300x300` : this.entity.entityImage
    }
  },
  watch: {
    menu(newState) {
      if (newState && this.entity === false) {
        this.isLoading = true
        this.$axios('entity/' + this.type + '/' + this.id).then(response => {
          this.entity = response.data
          this.isLoading = false
        }, () => {
          this.entity = null
          this.isLoading = false
        })
      }
    }
  },
  methods: {
    toRoute() {
      return entityToRoute(this.entity)
    },
  },
}
