
import PoolEntitiesGrid from './PoolEntitiesGrid.vue';
import PoolEntitiesCalendar from './PoolEntitiesCalendar.vue';
import PoolEntitiesList from './PoolEntitiesList.vue';
import PoolEntitiesSortToolbar from './PoolEntitiesSortToolbar.vue';
import PoolEntitiesStream from './PoolEntitiesStream.vue';
export default {
  components: {PoolEntitiesGrid, PoolEntitiesCalendar, PoolEntitiesList, PoolEntitiesSortToolbar, PoolEntitiesStream},
  props: {
    pool: Object,
    triggers: {
      type: Array,
      default:() => []
    },
    timetable: {
      type: Array,
      default: () => []
    },
    addButton: {
      type: Boolean,
      default: true,
    },
    deleteButton: {
      type: Boolean,
      default: true,
    },
    reorderButton: {
      type: Boolean,
      default: true,
    },
    automationButton: {
      type: Boolean,
      default: true,
    }
  },
  data() {
    return {
      isLoading: true,
      selectableBottomSheet: false,
      items: [],
      // data condition
      totalItems: 0,
      filtersToParam: '',
      sort: this.$route.query?.sort || '-id',
      page: this.$route.query?.page || 1,
      scope: this.$moment().format('YYYY-MM-DD'),
      pages: 0,
    }
  },
  async fetch() {
    await this.fetchData();
  },
  computed:{
    isEditable() {
      return this.pool.poolUserRef.can_update == 1 && !this.pool.is_readonly
    },
    isDeletable() {
      return this.pool.type==2 && this.pool.poolUserRef.can_delete == 1 && !this.pool.is_readonly && this.pool.typeEntity.table_type == 'schema'
    }
  },
  watch: {
    selectableBottomSheet() {
      this.$nextTick(() => {
        this.$refs.sheet.showScroll();
        // either set :retain-focus="false" above or do this:
        // this.$nextTick(() => this.$refs.sheet.unbind());
      });
    }
  },
  created() {
    this.$nuxt.$on('queueDone', (e) => {
      this.fetchData()
    })
  },
  beforeDestroy() {
    this.$store.commit('hub/clearSelectedEntityItem')
    this.$nuxt.$off('queueDone')
  },
  methods: {
    async moveSelectedEntityItemsToTrash() {
      this.selectableBottomSheet = false
      const response = await this.$axios.post('schemas/' + this.pool.typeEntity.table_type_pk_id + '/trash', this.$store.state.hub.selectedEntityItems)
      this.$store.commit('hub/clearSelectedEntityItem')
      this.$store.dispatch('successQueue/start', {
        id: response.data.lastQueueId,
        title: 'Inhalte in den Papierkorb verschoben',
        closeOnly: true,
      })
    },
    async filtersUpdate(value) {
      let queryString = '';
      value.forEach(item => {
        queryString = queryString + '&' + item.param
      })
      this.filtersToParam = queryString

      await this.fetchData()
    },
    async goToList(type) {
      if (type != this.pool.default_display_type) {
        // the type has changed, ensure to refetch data because other the calendar could make troubles with scoped data slots
        this.pool.default_display_type = type
        this.items = []
        await this.fetchData()
      }
    },
    changeSort(value) {
      this.sort = value
      this.page = 1
      this.$router.push({ path: this.$router.path, query: {page: this.page, sort: this.sort}})
      this.fetchData()
    },
    changePage(value) {
      if (this.page != value) {
        this.$router.push({ path: this.$router.path, query: {page: value, sort: this.sort}})
        this.page = value
        this.$fetch()
      }
    },
    changeScope(value) {
      this.scope = value
      this.fetchData()
    },
    async fetchData() {
      let expand = 'entityTitle,entityImage,entityStatus,entityId,entityType,entityImageProxyAllowed';
      if (this.pool.type == 1) {
        expand = expand + ',entityTypeLabel,entityTimeStart,createdBy,subtitle'
      } else {
        expand = expand + ',entityTimeStart,entityTimeEnd'
      }
      this.isLoading = true
      const response = await this.$axios('pools/' + this.pool.id + '/entities?expand=' + expand + this.filtersToParam + '&fields=id,subtitle,is_visible,created_at&per-page=24&page=' + this.page + '&scope=' + this.scope + '&type=' + this.pool.default_display_type + '&sort=' + this.sort);
      this.items = response.data.map(item => {
        item.triggers = false
        item.upcoming_removals = 0
        return item
      });
      if (response.headers.hasOwnProperty('x-pagination-current-page')) {
        this.page = +response.headers['x-pagination-current-page']
      }
      if (response.headers.hasOwnProperty('x-pagination-page-count')) {
        this.pages = +response.headers['x-pagination-page-count'];
      }
      if (response.headers.hasOwnProperty('x-pagination-total-count')) {
        this.totalItems = response.headers['x-pagination-total-count']
      }
      this.isLoading = false
    }
  }
}
