
export default {
  props: {
    filter: {
      type: Object,
      default: () => {}
    }
  },
  data:() => ({
    menu: false,
    model: null,
    items: [],
    searchQuery: '',
    loading: false,
  }),
  watch: {
    searchQuery: function(value) {
      if (value) {
        this.loadData(value)
      }
    }
  },
  methods: {
    sendEvent(value) {
      if (!value) {
        this.$emit('removed')
      } else {
        this.$emit('changed', `${this.filter.relationFilterName}=${value.entityId}`)
      }
    },
    loadData(query) {
      this.loading = true
      this.$axios.get(this.filter.api + '?expand=entityTitle,entityId&fields=id&search=' + query).then(response => {
        this.items = response.data
        this.loading = false
      })
    }
  }
}
