
import StreamAdd from './StreamAdd.vue';
import draggable from "vuedraggable";
import StreamItemScheduleRemoval from './StreamItemScheduleRemoval.vue';
import DeleteConfirm from './DeleteConfirm.vue';
import StreamItemTrigger from './StreamItemTrigger.vue';
import { entityToRoute } from '@/helpers/router.js'
import UserProfileBadge from './UserProfileBadge.vue';
import EntityHoverAsync from './EntityHoverAsync.vue';
export default {
  components: {StreamAdd, draggable, StreamItemScheduleRemoval, DeleteConfirm, StreamItemTrigger, UserProfileBadge, EntityHoverAsync},
  props: {
    items: {
      type: Array,
      default: () => []
    },
    triggers: {
      type: Array,
      default:() => []
    },
    timetable: {
      type: Array,
      default: () => []
    },
    pool: {
      type: Object,
      required: true,
    },
    addButton: {
      type: Boolean,
      default: true,
    },
    deleteButton: {
      type: Boolean,
      default: true,
    },
    reorderButton: {
      type: Boolean,
      default: true,
    },
    automationButton: {
      type: Boolean,
      default: true,
    },
    isLoading: {
      type: Boolean,
      default: true,
    }
  },
  data:() => ({
    stream: false,
    menu: null,
    isDisabled: false,
  }),
  async fetch() {
    if (this.pool.type == 1) {
      const response = await this.$axios('pools/' + this.pool.id + '/entity?expand=entityTypes.name')
      this.stream = response.data
    }
  },
  computed:{
    loader() {
      return this.isLoading || this.isDisabled
    },
    maxItemsIndex() {
      let maxItems = this.pool.typeEntity.max_items
      this.items.forEach((item, itemindex) => {
        if ((!item.is_visible || !item.entityStatus) && itemindex < maxItems) {
          maxItems++
        }
      })
      return maxItems - 1
    },
    itemsWithTimetable() {
      this.items.map( item => {
        item.upcoming_removals = this.timetable.filter(timetable => {
          // transform "schemarow" to "schema" in order to match
          const timetableEntityType = timetable.entityType == 'schemarow' ? 'schema' : timetable.entityType
          return timetable.action == 'remove' && timetableEntityType == item.entityType && timetable.entityId == item.entityId
        })
      })

      return this.items
    },
  },
  methods: {
    extractRoute(item) {
      let route = entityToRoute(item)
      if (route && Number.isInteger(this.pool.id)) {
        route.query = {ref: this.pool.id}
      }
      this.$router.push(route)
    },
    async onEnd() {
      this.isDisabled = true
      const values = this.items.map((item,index) => {
        return {id: item.id, index: index};
      })
      await this.$axios.put('streams/' + this.pool.type_pk_id + '/reorder', values)
      this.isDisabled = false
    },
    closeAndRefresh(entity) {
      this.isDisabled = true
      entity.triggers = false
      this.blockForceAndReload()
    },
    scheduledItem() {
      this.isDisabled = true
      this.blockForceAndReload()
    },
    async remove(item) {
      this.isDisabled = true
      await this.$axios.delete('pools/' + this.pool.id + '/entity/' + item.id)
      this.blockForceAndReload()
    },
    blockForceAndReload() {
      this.$emit('refetch')
      this.$emit('fetchPoolInfos')
      this.isDisabled = false
    },
    newEntry() {
      this.isDisabled = true
      this.$emit('refetch')
      this.$emit('fetchPoolInfos')
      this.isDisabled = false
    },
    hasActiveTrigger(entity) {
      return this.triggers.find(item => entity.id == item.id)
    },
  }
}
