
export default {
  props: {
    id: [String, Number],
    type: String,
    inactive: {
      type: String,
      default: 'grey',
    },
    fab: {
      type: Boolean,
      default: true
    },
    small: {
      type: Boolean,
      default: false
    },
    block: {
      type: Boolean,
      default: false
    }
  },
  data:() => ({
    isLoading: false,
  }),
  computed: {
    pinIndex: function() {
      return this.$store.state.me.pins.findIndex(item => {
        return item.entity_id == this.id && item.entity_type == this.type
      })
    }
  },
  methods: {
    async togglePin() {
      this.isLoading = true
      const index = this.pinIndex
      if (index == -1) {
        await this.savePin()
      } else {
        await this.removePin(index)
      }
      this.isLoading = false
    },
    async savePin() {
      await this.$store.dispatch('me/pin', {id:this.id, type:this.type})
      this.$store.dispatch('snackbar/success', 'Eintrag wurde gepinnt.');
      await this.$store.dispatch('me/fetch')
    },
    async removePin(index) {
      await this.$store.dispatch('me/removePin', {index:index, id:this.$store.state.me.pins[index].id});
      this.$store.dispatch('snackbar/success', 'Pin wurde entfernt');
      await this.$store.dispatch('me/fetch')
    }
  }
}
