
import { entityToRoute } from '@/helpers/router.js'
import EntityHoverAsync from './EntityHoverAsync.vue'
export default {
  components: {  EntityHoverAsync },
  props: {
    pool: {
      type: Object,
      default:() => {}
    },
    items: {
      type: Array,
      default: () => []
    },
    isLoading: {
      type: Boolean,
      default: false
    },
    isEditable: {
      type: Boolean,
      default: false
    },
    scope: {
      type: [Number, String],
      default: 0
    }
  },
  data: () => ({
    type: 'month',
    types: [{value: 'month', text: 'Monat'}, {value: 'week', text: 'Woche'}, {value: 'day', text: 'Tag'}],
    mode: 'stack',
    modes: [{value: 'stack', text: 'Gestapelt'}, {value: 'column', text: 'Spalten'}],
  }),
  computed: {
    events() {
      const events = []
      this.items.forEach(item => {
        events.push({
          name: item.entityTitle,
          start: item.entityTimeStart * 1000,
          end: item.entityTimeEnd * 1000,
          timed: true,
          item: item
        })
      })
      return events
    },
    monthNow() {
      if (this.type == 'day') {
        return this.$moment(this.scope, 'YYYY-MM-DD', true).format("dd D, MMMM YYYY")
      }
      return this.$moment(this.scope, 'YYYY-MM-DD', true).format("MMMM YYYY")
    },
  },
  methods: {
    prev () {
      this.$refs.calendar.prev()
    },
    next () {
      this.$refs.calendar.next()
    },
    openDay(value) {
      this.type = 'day';
      this.$emit('scopeChange', value.date);
    },
    goToItem: function(value) {
      const item = value.event.item
      let route = entityToRoute(item)
      if (route && Number.isInteger(this.pool.id)) {
        route.query = {ref: this.pool.id}
      }
      this.$router.push(route)
    },
    scopeChanger: function(v) {
      this.$emit('scopeChange', v)
    }
  }
}
