
import PoolEntitiesSortToolbarFilter from './PoolEntitiesSortToolbarFilter.vue';
export default {
  components: { PoolEntitiesSortToolbarFilter },
  props: {
    listType: {
      type: [String, Number],
      default: ""
    },
    sort: {
      type: [String, Number],
      default: 0
    },
    pool: {
      type: Object,
      default: () => {}
    },
    filters: {
      type: Array,
      default:() => [],
    },
    isDeletable: {
      type: Boolean,
      default: false,
    }
  },
  data:() => ({
    menu: false,
    selectableBottomSheet: false,
    activeFilters: [],
    sorts: [
      {value: '-id', label: 'Neu zu Alt', icon: 'icon-sort-descending'},
      {value: 'id', label: 'Alt zu Neu', icon: 'icon-sort-ascending'},
      {value: 'title', label: 'A-Z', icon: 'icon-sort-alpha-asc'},
      {value: '-title', label: 'Z-A', icon: 'icon-sort-alpha-desc'},
      {value: '-updated_at', label: 'Zuletzt aktualisiert', icon: 'icon-clock'},
    ]
  }),
  computed: {
    buttonLabel() {
      return this.sorts.find(i => i.value == this.sort).label
    },
    buttonIcon() {
      return this.sorts.find(i => i.value == this.sort).icon
    }
  },
  methods: {
    toggleDeleteBottomSheet() {
      this.selectableBottomSheet = !this.selectableBottomSheet
      this.$emit('toggleSelectableBottomSheet', this.selectableBottomSheet)
    },
    applyFilter(filterValue, filterIndex) {
      this.activeFilters[filterIndex] = {
        param: filterValue
      }
      this.$emit('filtersUpdate', this.activeFilters)
    },
    removeFilter(filterIndex) {
      this.activeFilters.splice(filterIndex, 1)
      this.$emit('filtersUpdate', this.activeFilters)
    },
    switchSort(sortParam) {
      this.$emit('changeSort', sortParam)
    },
    goToList(type) {
      this.$emit('goToList', type)
    }
  }
}
