
import ModalFooter from '../ModalFooter.vue'
import ModalToolbar from '../ModalToolbar.vue'
export default {
  components: { ModalToolbar, ModalFooter},
  props: {
    entityId: {
      type: [Number, String],
      default: ""
    },
    entityType: {
      type: [Number, String],
      default: ""
    },
    streamId: {
      type: [Number, String],
      default: ""
    }
  },
  data:() => ({
    dateOut: null,
    dateIn: null,
    isLoading: false
  }),
  computed: {
    dateNow : function() {
      return this.$moment().format('YYYY-MM-DD')
    }
  },
  methods: {
    minNow() {
      return this.$moment().format("YYYY-MM-DD")
    },
    minDate() {
      return this.$moment(this.dateIn).format("YYYY-MM-DD")
    },
    async save() {
      this.isLoading = true
      await this.$axios.post('streams/' + this.streamId + '/item-create', {
        pk_id: this.entityId,
        type: this.entityType,
        date_in: this.dateIn,
        date_out: this.dateOut ? this.dateOut : null,
      })
      this.$store.dispatch('snackbar/success', 'Eintrag wurde geplant.');
      this.isLoading = false
      this.$emit('scheduleditem', true)
      this.$emit('close', true)
    }
  }
}
