
export default {
  props: {
    color: {
      type: [String],
      default: null,
    },
    label: {
      type: String,
      default: ""
    },
    image: {
      type: [String, Boolean],
      default: false,
    },
    userId: {
      type: [Number, String],
      default: ""
    },
    dense: {
      type: Boolean,
      default: false,
    }
  },
  data:() => ({
    isVisible: false,
    user: false,
    isLoading: false,
    system: false,
    img: false,
  }),
  computed: {
    hasAddress() {
      if (this.user.street || this.user.street_nr || this.user.zip || this.user.city) {
        return true
      }
      return false
    },
    themeColor() {
      if (this.color == null) {
        return this.$vuetify.theme.dark ? 'grey darken-3' : 'grey darken-1'
      }

      return this.color
    }
  },
  watch: {
    isVisible(newState) {
      if (newState && this.user === false) {
        this.isLoading = true
        this.$axios('users/' + this.userId).then(response => {
          this.user = response.data
          this.isLoading = false
        }, () => {
          this.user = null
          this.isLoading = false
        })
      }
    }
  },
  created() {
    if (this.userId == -1) {
      this.system = true
      this.img = require(`~/assets/flyoavatar.png`)
    } else {
      this.img = this.image
    }
  }
}
