
import PoolEntities from "../../../../components/views/PoolEntities.vue"
import { Pools } from '~/helpers/constants.js'
import { entityToRoute } from '@/helpers/router.js'
import DeleteConfirm from '../../../../components/views/DeleteConfirm.vue'
import CrossViewConflict from '../../../../components/views/CrossViewConflict.vue'
import ExpansionPanelSidebar from '../../../../components/ExpansionPanelSidebar.vue'
import EntityCommentsComments from '../../../../components/views/EntityCommentsComments.vue'
export default {
  name: "Poolview",
  components: { PoolEntities, DeleteConfirm, CrossViewConflict, ExpansionPanelSidebar, EntityCommentsComments},
  props: {
    pool: {
      type: Object,
      default: () => {}
    }
  },
  data:() => ({
    focus: new Date(),
    timelineMode: 1,
    timetable: [],
    triggers: [],
    isLoading: true,
    currentMonth: '',
    denseSidebar: false,
  }),
  async fetch() {
    this.$store.commit('hub/breadcrumbs', [
      {text: "Content Pools", to: {name: 'pools-index'}},
      {text: this.pool.name, to: {name: 'pools-index-id', params: this.$route.params}}
    ]);
    this.isLoading = true;
    if (this.pool.type == Pools.TYPE_STREAM ) {
      const timetable = await this.$axios('pools/' + this.pool.id + '/timeline?expand=user,entityTitle,entityId,entityType')
      this.timetable = timetable.data.map(item => {
        item.details = false;
        return item
      })

      const triggers = await this.$axios('pools/' + this.pool.id + '/triggers')
      this.triggers = triggers.data
    }
    this.isLoading = false
  },
  computed: {
    scheduledItems: function() {
      const items = []
      this.timetable.forEach(item => {
        const start = item.time_scheduled * 1000
        const end = item.time_scheduled * 1000

        // when there is a action type = "insert" before type = "remove"
        // we do not add another item, we change end date from the previous index, if any.
        // ps: we always assume the items are sorted based on the time in point where the action is running
        let hasPreviousItem = items.findIndex(findItem => {
          if (findItem.entityId == item.entityId && findItem.entityType == item.entityType && findItem.hasPreviousItem === false && findItem.action == 'insert' && item.action == 'remove') {
            return true
          }
          return false;
        })

        if (hasPreviousItem !== -1) {
          items[hasPreviousItem].end = start
          items[hasPreviousItem].hasPreviousItem = hasPreviousItem
          items[hasPreviousItem].color = 'primary'
        } else {
          items.push({
            start: start,
            end: end,
            action: item.action,
            entityId: item.entityId,
            entityType: item.entityType,
            hasPreviousItem: false,
            timed: true,
            color: item.action == 'remove' ? 'red' : 'success',
            name: item.entityTitle
          })
        }
      })
      return items
    }
  },
  watch: {
    timelineMode: function(n) {
      this.$nextTick(function () {
        this.checkCalendar()
      })
    }
  },
  methods: {
    checkCalendar() {
      if (this.$refs.calendar) {
        this.$refs.calendar.checkChange()
        this.currentMonth = this.$moment(this.focus).format("MMMM YYYY")
      }
    },
    prev() {
      this.$refs.calendar.prev()
      this.checkCalendar()
    },
    next() {
      this.$refs.calendar.next()
      this.checkCalendar()
    },
    async removeScheduledItem(item,index) {
      const response = await this.$axios.delete('streams/' + this.pool.type_pk_id + '/job/' + item.id)
      this.$store.dispatch('snackbar/success', 'Der Eintrag wurde aus dem Fahrplan entfernt.');
      this.timetable.splice(index, 1)
    },
    extractRoute(item) {
      this.$router.push(entityToRoute(item))
    }
  }
}
