
import HubDatetimepicker from '../attributes/HubDatetimepicker.vue';
import ModalFooter from '../ModalFooter.vue';
import ModalToolbar from '../ModalToolbar.vue';
export default {
  components: { HubDatetimepicker, ModalToolbar, ModalFooter },
  props: {
    id: {
      type: [Number, String],
      default: ""
    },
    streamId: {
      type: [Number, String],
      default: ""
    },
  },
  data:() => ({
    dateOut: null,
    isSaving: false
  }),
  computed: {
    dateNow : function() {
      return this.$moment().format('YYYY-MM-DD')
    }
  },
  methods: {
    async insertScheduled() {
      this.isSaving = true
      await this.$axios.post('streams/' + this.streamId + '/schedule-item-removal/' + this.id, {date_out: this.dateOut})
      this.$store.dispatch('snackbar/success', 'Entfernung wurde geplant.');
      this.$emit('scheduleditem', true)
      this.isSaving = false
      this.$emit('close', true)
    }
  }
}
