export function entityToRoute(item) {
  let route = null;
  if (item.entityType == 'schema' || item.entityType == 'schemarow') {
    route = 'schemas-rows-id'
  } else if (item.entityType == 'user') {
    route = 'content-users-id'
  } else if (item.entityType == 'integrationplatform') {
    route = 'platforms-id'
  } else if (item.entityType == 'pool') {
    route = 'pools-index-id'
  } else if (item.entityType == 'file') {
    route = 'filesmanager-id'
  } else if (item.entityType == 'integration') {
    return {name: 'platforms-id-integrations-integration', params: {id: parseInt(item.item.entity.platform_id), integration: parseInt(item.entityId)}}
  } else if (item.entityType == 'tag') {
    route = 'organisation-tags-id'
  } else {
    // ensure for entity type file
    return ''
  }

  return { name: route, params: { id: parseInt(item.entityId) }};
}
