
import ModalUseCloseFooter from '../ModalUseCloseFooter.vue'
export default {
  name: 'VDatetimePicker',
  components: { ModalUseCloseFooter },
  props: {
    value: {
      type: [Date, String],
      default: null
    },
    clearable: {
      type: Boolean,
      default: true,
    },
    dense : {
      type: Boolean,
      default: false,
    },
    disabled: {
      type: Boolean
    },
    loading: {
      type: Boolean
    },
    label: {
      type: String,
      default: ''
    },
    dialogWidth: {
      type: Number,
      default: 380
    },
    hint: {
      type: String,
      default: ''
    },
    textFieldProps: {
      type: Object,
    },
    timePickerProps: {
      type: Object,
    },
    datePickerProps: {
      type: Object,
    },
    hideDetails: {
      type: Boolean,
      default: false,
    },
    rules: {
      type: Array,
      default: () => []
    },
    errorMessages: {
      type: [Array, String],
      default: () => []
    }
  },
  data() {
    return {
      display: false,
      activeTab: 0,
      date: null,
      time: '00:00:00'
    }
  },
  computed: {
    isDisabled() {
      return !this.date || !this.time
    },
    formattedDatetime() {
      return this.value ? this.$moment(this.value).format('DD.MM.YYYY HH:mm') : ''
    },
    dateSelected() {
      return !this.date
    },
    currentMoment() {
      if (this.date && this.time) {
        let datetimeString = this.date + ' ' + this.time
        if (this.time.length === 5) {
          // add missing seconds
          datetimeString += ':00'
        }
        return this.$moment(datetimeString, "YYYY-MM-DD HH:mm:ss") // format same according to init()
      }

      return null;
    }
  },
  watch: {
    value() {
      this.init()
    }
  },
  mounted() {
    this.init()
  },
  methods: {
    init() {
      if (!this.value) {
        return
      }
      const moment = this.$moment(this.value);
      const date = moment.format('YYYY-MM-DD') // internal used iso formats, but not stored at the end
      const time = moment.format('HH:mm:ss') // internal used iso formats, but not stored at the end
      this.date = date
      this.time = time
    },
    okHandler() {
      this.resetPicker()
      this.$emit('input', this.currentMoment.toISOString())
      // https://stackoverflow.com/a/28641878
      // --> toISOString()
      // --> format()
    },
    clearInput() {
      this.date = null
      this.time = '00:00:00'
      this.$emit('input', null)
    },
    closeHandler() {
      this.resetPicker()
    },
    resetPicker() {
      this.display = false
      this.activeTab = 0
      if (this.$refs.timer) {
        this.$refs.timer.selectingHour = true
      }
    },
    showTimePicker() {
      this.activeTab = 1
    }
  }
}
