
import tippy from 'tippy.js'
import { Editor, EditorContent, BubbleMenu, VueRenderer, mergeAttributes } from '@tiptap/vue-2'
import StarterKit from '@tiptap/starter-kit'
import Mention from '@tiptap/extension-mention'
import CommentTextareaMention from './CommentTextareaMention.vue'
import { BubbleMenu as BubbleMenuPlugin } from '@tiptap/extension-bubble-menu'

const CustomMention = Mention.extend({
  renderHTML({ node, HTMLAttributes }) {
    return [
      'span',
      mergeAttributes(this.options.HTMLAttributes, HTMLAttributes),
      `${node.attrs.name}`
    ]
  },
  parseHTML() {
    return [
      {
        tag: 'span[data-mention-id]',
      },
    ]
  },
  addAttributes() {
    return {
      name: {
        default: null,
        parseHTML: element => {
          return {
            name: element.getAttribute('data-mention-name'),
          }
        },
        renderHTML: attributes => {
          if (!attributes.name) {
            return {}
          }
          return {
            'data-mention-name': attributes.name,
          }
        },
      },
      id: {
        default: null,
        parseHTML: element => {
          return {
            id: element.getAttribute('data-mention-id'),
          }
        },
        renderHTML: attributes => {
          if (!attributes.id) {
            return {}
          }
          return {
            'data-mention-id': attributes.id,
          }
        },
      },
    }
  },
})

export default {
  components: {
    EditorContent, BubbleMenu
  },
  props: {
    value: {
      type: [String,Object],
      default: '',
    },
  },
  data:() => ({
    editor: null,
  }),
  watch: {
    value(value) {
      // HTML
      const isSame = this.editor.getHTML() === value

      // JSON
      // const isSame = this.editor.getJSON().toString() === value.toString()

      if (isSame) {
        return
      }

      this.editor.commands.setContent(this.value, false)
    },
  },
  mounted() {
    this.editor = new Editor({
      extensions: [
        StarterKit,
        BubbleMenuPlugin.configure({
          pluginKey: 'buzzesCommentBubbleMenu'
        }),
        CustomMention.configure({
          HTMLAttributes: {
            class: 'mention',
          },
          suggestion: {
            items: async function(query) {
              const response = await this.$axios('organisation/admins?query=' + encodeURIComponent(query.query) + '&fields=user.id,user.firstname,user.lastname,user.email,user.is_admin&sort=hb_user.firstname')
              let values = []
              const data = response.data.filter(item => !item.user.is_admin)
              data.forEach(i => {
                values.push({id: i.user.id, name: i.user.firstname, full: `${i.user.firstname} ${i.user.lastname}`, email: i.user.email})
              })
              return values
            }.bind(this),
            render: () => {
              let component
              let popup

              return {
                onStart: props => {
                  component = new VueRenderer(CommentTextareaMention, {
                    parent: this,
                    propsData: props,
                  })

                  if (!props.clientRect) {
                    return
                  }

                  popup = tippy('body', {
                    getReferenceClientRect: props.clientRect,
                    appendTo: () => document.getElementById('app'),
                    content: component.element,
                    showOnCreate: true,
                    interactive: true,
                    trigger: 'manual',
                    placement: 'bottom-start',
                  })
                },
                onUpdate(props) {
                  component.updateProps(props)

                  if (!props.clientRect) {
                    return
                  }

                  popup[0].setProps({
                    getReferenceClientRect: props.clientRect,
                  })
                },
                onKeyDown(props) {
                  if (props.event.key === 'Escape') {
                    popup[0].hide()

                    return true
                  }

                  return component?.ref?.onKeyDown(props)
                },
                onExit() {
                  popup[0].destroy()
                  component.destroy()
                },
              }
            },
          },
        })
      ],
      editorProps: {
        transformPastedHTML(html) {
          // remove all html tags from pasted content
          return html.replace(/(<([^>]+)>)/gi, "");
        },
      },
      content: this.parsedValue,
      onUpdate: () => {
        // HTML
        this.$emit('input', this.editor.getHTML())

        // JSON
        // this.$emit('input', this.editor.getJSON())
      },
    })
  },

  beforeDestroy() {
    this.editor?.destroy()
  }
}
