/**
 * Single Integrations hold the status and health of the integration on the integration object itself, and not on the integration items
 * @param {int} type
 * @returns
 */
export function IsSingleIntegration(type) {
  const types = [Integration.TYPE_NITRO, Integration.TYPE_NITRO_CMS, Integration.TYPE_VENUE, Integration.TYPE_API_SDK, Integration.TYPE_MAP, Integration.TYPE_SIGNAGEOS, Integration.TYPE_INDESIGN, Integration.TYPE_MAILJET_TEMPLATE, Integration.TYPE_ALGOLIA]
  return types.includes(type)
}

export function IntegrationErrorsCount(integration)
{
  if (!Boolean(integration.is_active)) {
    return 0
  }

  let count = 0

  if (IsSingleIntegration(integration.type)) {
    if (Boolean(integration.is_configured) && !Boolean(integration.is_tested)) {
      return 1
    }

    return 0
  }

  integration.items.forEach(item => {
    if (Boolean(item.is_configured) && !Boolean(item.is_tested)) {
      count++
    }
  })
  return count
}

export const Event = {
  prices: function() {
    let values = [
      {text: 'Keine Angabe', value: ''},
      {text: 'Kostenlos', value:'Kostenlos' },
      {text: 'Kollekte', value: 'Kollekte'},
      {text: 'Würfeln', value: 'Würfeln'},
    ]

    let i;
    for (i = 1; i < 100; i++) {
      values.push({
        text: `${i}.-`,
        value: i
      })
    }

    values.push({
      text: 'Über 100.-',
      value: 'Über 100 CHF'
    })

    return values;
  },
  ages: function() {
    let values = [
      {text:'Keine Beschränkung', value: 0},
    ]

    let i;
    for (i = 1; i < 65; i++) {
      values.push({
        text: `Ab ${i} Jahren`,
        value: i
      })
    }

    return values;
  }
}
