
import { entityToRoute } from '@/helpers/router.js'
export default {
  props: {
    pool: {
      type: Object,
      default:() => {}
    },
    items: {
      type: Array,
      default: () => []
    },
    page: {
      type: [String, Number],
      default: 1
    },
    pages: {
      type: [String, Number],
      default: 1
    },
    isLoading: {
      type: Boolean,
      default: false
    },
    totalItems: {
      type: [String, Number],
      default: 0
    },
    isEditable: {
      type: Boolean,
      default: false
    },
    isSelectable: {
      type: Boolean,
      default: false,
    }
  },
  methods: {
    toRoute(item) {
      let route = entityToRoute(item)
      if (route && Number.isInteger(this.pool.id)) {
        route.query = {ref: this.pool.id}
      }
      return route
    },
    toggleSelectable(item) {
      this.$store.commit('hub/toggleSelectedEntityItem', item)
    }
  }
}
