
export default {
  props: {
    value: {
      type: Boolean,
      default: false,
    },
    showButton: {
      type: Boolean,
      default: false
    }
  },
  data:() => ({
    panels: [],
    isDense: false,
  }),
  mounted() {
    this.isDense = this.value
  },
  methods: {
    toggleDense() {
      this.isDense = !this.isDense
      this.$emit('input', this.isDense)
    }
  }
}
